* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    transition: all 0.2s; 
}


/* Header */


.navigation {
    margin: 0px;
    position: fixed;
    top: 0;
    z-index: 2;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.45);
}

.menulist{
    &--styling {
    margin: 30px 8%;
    margin-bottom: 20px;
    list-style-type: none;
    padding-left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    }

    &__item {
    font-size: 16px;
    font-size: 16px;
    font-weight: 700;
    padding:10px 12px;
        &--logo {
        padding: 0px;
        font-family: 'Pacifico', cursive;
        font-size: 50px;
        margin-right: 40%;
        }
    }
}   

.navigation__link {
    text-decoration: none;
    color: #000;
    padding-top: 6px;
    &:hover {
    border-top: 3px solid #ff6033;
    cursor: pointer;
    }
}

[name=headerlist] {
    display: none;
    position: fixed;
    top: 50px;
    right: 80px;
}

.menulist__icon {
    display: none;
    position: fixed;
    top: 45px;
    right: 5%;
    width: 30px;
    height: 30px;
}

@media screen and (max-width:1020px) {
    .menulist__icon{
        display: inline-block;
        background-image: url(../web/image/hamburger.png);
        background-size: contain;
        background-repeat: no-repeat;

        &:hover {
            cursor: pointer;
        }
    }

    .menulist--styling {
        li{ display: none;}

        .menulist__item {
            &--logo {
            display: inline-block;
            margin-right: 70%;
            }
        }
    }

    #menulist:checked + .menulist .menulist--styling li {
        display: block;
        background-color: goldenrod;
    }

}
/* Fresh Life */



.FreshLife {
    background-image: url("../web/image/img_header/strawberry.jpg");
    background-size: cover;
    background-position: center;
    min-height: 1000px;
    font-family: 'Raleway', sans-serif;
    position: relative;
    margin-top: -50px;
    .scroll {
        text-align: center;
        position: absolute;
        bottom: 60px;
        left: 0;
        right: 0;
    }
}

.FreshLife-block__title {
    margin-left: 11%;
    padding-top: 360px;
    margin-bottom: 0;
    font-size: 60px;
    font-weight: 900;
    max-width: 290px;
}

.FreshLife-block__text {
    max-width: 375px;
    min-height: 75px;
    margin-left: 11%;
    margin-top: 31px;
    margin-bottom: 0;
    font-size: 17px;
    font-weight: 300;
}

.FreshLife__button{
    width: 200px;
    height: 55px;
    background-color: #ff6033;
    border: transparent;
    color: #fff;
    margin-left: 11%;
    margin-top: 30px;
    font-size: 20px;
    font-weight: 300;
    box-shadow: 0 2px 5px  rgb(46, 46, 46);
    transition-duration:  0.7s;

    &:hover {
    cursor: pointer;
    transform: rotateX(360deg) skewX(-15deg);
    background-color: #008000;
    border-bottom-right-radius: 25px;
    border-top-left-radius: 25px;
    }
}


/* About Us */




.AboutUs {
    background-image: url("../web/image/img_header/backgr-blue.png");
    background-size: cover;
    background-position: center;
    min-height: 700px;
    font-family: 'Raleway', sans-serif;
    color: #fff;

    &__flex{
        display: flex;
        flex-flow: row wrap-reverse;
        justify-content: space-around;
        align-items: center;
    }
    &-block {
        margin: 0 auto;
        max-width: 503px;
        &__title {
        margin: 0;
        font-size: 90px;
        font-weight: 700; 
        max-width: 483px;
        }
        &__text {
            font-size: 17px;
            font-weight: 300;
            max-width: 483px;
            padding: 20px 0;
            &--width { 
                max-width: 503px;
            padding: 0;}
        }
    }

    &__photo {
    max-width: 630px;
    min-width: 310px;
    margin: 0 auto;
    padding-top: 15px;
    
        img{
        width: 100%;
        }
}
}
